import { _decorator, Component } from 'cc';
import { GameStateMachine } from './gameStateMachine/GameStateMachine';
import { GameStates } from './gameStateMachine/StatesEnum';

const { ccclass, property } = _decorator;

@ccclass('GameModelComponent')
export class GameModelComponent extends Component {

    private gameStateMachine: GameStateMachine;

    start() {
        this.gameStateMachine = new GameStateMachine(this, GameStates);
        this.gameStateMachine.setState(GameStates.BUILDING);
    }

}

